      getLangResource () {
        this.$http.get('static/lang/language.' + this.language + '.json').then(response => {
          this.text = response.body
        }, err => {
          console.log(err)
        })
      },
      switchLang (langCode) {
        this.language = langCode
        this.getLangResource()
      }
    },
