import Vue from 'vue'

import {
  Vuetify,
  VApp,
  VNavigationDrawer,
  VFooter,
  VList,
  VCard,
  VBtn,
  VBtnToggle,
  VIcon,
  VGrid,
  VToolbar,
  VCarousel,
  VParallax,
  VExpansionPanel,
  transitions
} from 'vuetify'

import router from './router'
import './css/config.styl'
import './css/main.css'

import App from './App'
import Countdown from './components/Countdown'
import FlipCountdown from './components/FlipCountdown'
import Coverflow from 'vue-coverflow/lib/Coverflow'
import { VueMasonryPlugin } from 'vue-masonry'
import VueScrollTo from 'vue-scrollto'
import VueYouTubeEmbed from 'vue-youtube-embed'

import 'vue-touch-ripple/component.css'
import VueTouchRipple from 'vue-touch-ripple'

import VueResource from 'vue-resource'

Vue.component('countdown', Countdown)
Vue.component('flip-countdown', FlipCountdown)
Vue.component('coverflow', Coverflow)

Vue.use(Vuetify, {
  components: {
    VApp,
    VNavigationDrawer,
    VFooter,
    VList,
    VCard,
    VBtn,
    VBtnToggle,
    VIcon,
    VGrid,
    VToolbar,
    VCarousel,
    VParallax,
    VExpansionPanel,
    transitions
  },
  theme: {
    primary: 'rgb(47, 61, 138)',
    secondary: '#424242',
    accent: '#82B1FF',
    error: '#FF5252',
    info: '#2196F3',
    success: '#4CAF50',
    warning: '#FFC107'
  }
})
Vue.use(VueMasonryPlugin)
Vue.use(VueScrollTo, {
  container: 'body',
  duration: 500,
  easing: 'ease',
  offset: 0,
  cancelable: true,
  onDone: false,
  onCancel: false,
  x: false,
  y: true
})
Vue.use(VueTouchRipple)
Vue.use(VueYouTubeEmbed)
Vue.use(VueResource)

Vue.config.productionTip = false

Vue.filter('two_digits', function (value) {
  if (value.toString().length <= 1) {
    return '0' + value.toString()
  }
  return value.toString()
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  render: h => h(App)
})



