<template>
  <v-layout row>
    <v-flex xs12 class="text-xs-center">
      <div class="block">
        <flip-countdown :number="days"></flip-countdown>
        <p class="text color-dark">Days</p>
      </div>
    </v-flex>
    <v-flex xs12 class="text-xs-center">
      <div class="block">
        <flip-countdown :number="hours"></flip-countdown>
        <p class="text color-dark">Hours</p>
      </div>
    </v-flex>
    <v-flex xs12 class="text-xs-center">
      <div class="block">
        <flip-countdown :number="minutes"></flip-countdown>
        <p class="text color-dark">Minutes</p>
      </div>
    </v-flex>
    <v-flex xs12 class="text-xs-center">
      <div class="block">
        <flip-countdown :number="seconds"></flip-countdown>
        <p class="text color-dark">Seconds</p>
      </div>
    </v-flex>
  </v-layout>
</template>
<script>
  export default {
    name: 'Countdown',
    props: {
      date: {
        type: String
      }
    },
    data () {
      return {
        now: Math.trunc((new Date()).getTime() / 1000)
      }
    },
    mounted () {
      setInterval(() => {
        this.$data.now = Math.trunc((new Date()).getTime() / 1000)
      }, 1000)
    },
    computed: {
      modifiedDate () {
        return Math.trunc(Date.parse(this.date) / 1000)
      },
      seconds () {
        return (this.modifiedDate - this.now) % 60
      },
      minutes () {
        return Math.trunc((this.modifiedDate - this.now) / 60) % 60
      },
      hours () {
        return Math.trunc((this.modifiedDate - this.now) / 60 / 60) % 24
      },
      days () {
        return Math.trunc((this.modifiedDate - this.now) / 60 / 60 / 24)
      },
      time () {
        return {
          days: this.days,
          hours: this.hours,
          minutes: this.minutes,
          seconds: this.seconds
        }
      }
    }
  }
</script>
<style>
  .block {
    display: flex;
    flex-direction: column;
    margin: 20px;
  }
  .text {
    /* color: #1abc9c; */
    color: rgb(100, 116, 203);
    font-size: 35px;
    font-family: 'Roboto Condensed', serif;
    font-weight: 400;
    margin-top: 10px;
    margin-bottom: 10px;
    text-align: center;
    line-height: 40px;
  }
  .digit {
    /* color: #ecf0f1; */
    font-size: 100px;
    font-weight: 100;
    font-family: 'Roboto', serif;
    margin: 10px;
    text-align: center;
    line-height: 100px;
  }

  @media screen and (max-width: 599px) {
    .block {
      margin: 10px;
    }
    .text {
      font-size: 20px;
      line-height: 25px;
    }
    .digit {
      font-size: 50px;
      line-height: 50px;
    }
  }
</style>


