<template>
  <!-- <p class="digit color-dark">{{ number | two_digits }}</p> -->
  <div class="flip-clock-wrapper">
    <span v-show="show" class="flip-clock__piece">
      <span class="flip-clock__card flip-card">
        <b class="flip-card__top">{{ current | two_digits }}</b>
        <b class="flip-card__bottom" :data-value="currentStr"></b>
        <b class="flip-card__back" :data-value="previousStr"></b>
        <b class="flip-card__back-bottom" :data-value="previousStr"></b>
      </span>
    </span>
  </div>
</template>
<script>
  export default {
    name: 'FlipCountdown',
    props: {
      number: {
        type: Number
      }
    },
    mounted () {
      this.$data.current = this.number
      this.$data.show = true
      this.$data.classes = this.$el.classList
    },
    data () {
      return {
        current: 0,
        previous: 0,
        show: false,
        classes: null
      }
    },
    computed: {
      currentStr () {
        return this.$data.current <= 9 ? '0' + this.$data.current.toString() : this.$data.current.toString()
      },
      previousStr () {
        return this.$data.previous <= 9 ? '0' + this.$data.previous.toString() : this.$data.previous.toString()
      }
    },
    watch: {
      number: {
        handler: function (val, oldVal) {
          this.$data.previous = oldVal || 0
          this.$data.current = val || 0
          if (this.$data.classes) {
            this.$el.classList.remove('flip')
            void this.$el.offsetWidth
            this.$el.classList.add('flip')
          }
        },
        immediate: true
      }
    }
  }
</script>
<style>
  .flip-clock__piece {
    display: inline-block;
    margin: 0 0.2vw;
  }
  @media (min-width: 1000px) {
    .flip-clock__piece {
      margin: 0 5px;
    }
  }

  .flip-clock__slot {
    font-size: 1rem;
    line-height: 1.5;
    display: block;
  }

  .flip-card {
    display: block;
    position: relative;
    padding-bottom: 1em;
    font-size: 2.25rem;
    line-height: 0.95;
    box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.7);
    border-radius: 5px;
  }

  @media (min-width: 1000px) {
    .flip-clock__slot { font-size: 1.2rem; }
    .flip-card { font-size: 3rem; }
  }


  /*////////////////////////////////////////*/


  .flip-card__top,
  .flip-card__bottom,
  .flip-card__back-bottom,
  .flip-card__back::before,
  .flip-card__back::after {
    display: block;
    height: 1em;
    color: #ccc;
    background: #222;
    padding: 0;
    border-radius: 0.15em 0.15em 0 0;
    backface-visiblity: hidden;
    transform-style: preserve-3d;
    width: 1.8em;
    line-height: 2em;
    text-shadow: 0 0 10px rgba(0, 0, 0, 0.6);
  }

  .flip-card__bottom,
  .flip-card__back-bottom {
    color: #FFF;
    position: absolute;
    top: 50%;
    left: 0;
    border-top: solid 1px #000;
    background: #393939;
    border-radius: 0 0 0.15em 0.15em;
    pointer-events: none;
    overflow: hidden;
    z-index: 2;
  }

  .flip-card__back-bottom {
    z-index: 1;
  }

  .flip-card__bottom::after,
  .flip-card__back-bottom::after {
    display: block;
    margin-top: -1em;
  }

  .flip-card__back::before,
  .flip-card__bottom::after,
  .flip-card__back-bottom::after {
    content: attr(data-value);
  }

  .flip-card__back {
    position: absolute;
    top: 0;
    height: 100%;
    left: 0%;
    pointer-events: none;
  }

  .flip-card__back::before {
    position: relative;
    overflow: hidden;
    z-index: -1;
  }

  .flip .flip-card__back::before {
    z-index: 1;
    animation: flipTop 0.3s cubic-bezier(.37,.01,.94,.35);
    animation-fill-mode: both;
    transform-origin: center bottom;
  }

  .flip .flip-card__bottom {
    transform-origin: center top;
    animation-fill-mode: both;
    animation: flipBottom 0.6s cubic-bezier(.15,.45,.28,1); /* 0.3s; */
  }

  @keyframes flipTop {
    0% {
      transform: rotateX(0deg);
      z-index: 2;
    }
    0%, 99% {
      opacity: 1;
    }
    100% {
      transform: rotateX(-90deg);
      opacity: 0;
    }
  }

  @keyframes flipBottom {
    0%, 50% {
      z-index: -1;
      transform: rotateX(90deg);
      opacity: 0;
    }
    51% {
      opacity: 1;
    }
    100% {
      opacity: 1;
      transform: rotateX(0deg);
      z-index: 5;
    }
  }

  .flip-clock-wrapper {
    display: inline-block;
  }
</style>


